import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
// import Genealogy from '@/view/Member'
const Genealogy = () => import('@/view/Member')
// import MemberDetail from '@/view/MemberDetail'
const MemberDetail = () => import('@/view/MemberDetail')
// import GenealogyIndexes from '@/view/Genealogy'
const GenealogyIndexes = () => import('@/view/Genealogy')

Vue.use(Router)

const myRouter = new Router({
	// mode: 'history',
	// base: '/genealogy-font/',

	routes: [
		// {
		//   path: '/',
		//   name: 'HelloWorld',
		//   component: HelloWorld
		// },

		{
			path: '/',
			name: 'GenealogyIndexes',
			component: GenealogyIndexes,
			meta: { 'breadList': [{ 'path': '', 'name': '首页' }] }
		},

		{
			path: '/genealogy/:gid',
			name: 'Genealogy',
			component: Genealogy,
			meta: { 'breadList': [{ 'path': '/', 'name': '首页' }, { 'path': '', 'name': '家谱' }] }
		},

		// {
		// 	path: '/member',
		// 	name: 'MemberDetail',
		// 	component: MemberDetail
		// }
	],
})

// myRouter.beforeEach((to, from, next) => {
// 	console.log(to, from);
// 	next();
// })

export default myRouter;



