<template>
	<div id="app">
		<Layout>
			<Header :style="{ position: 'fixed', width: '100%' }">
				<img class="layout-logo" src="https://hongstrak.github.io/static_page/genealogy/icaon/yang.png" />

				<!-- <p class="layout-nav">百家谱</p> -->
				<h1 class="layout-nav">百家谱</h1>
			</Header>
			<Content on-collapse :style="{ margin: '88px 20px 0', minHeight: '700px' }">
				<Breadcrumb :style="{ margin: '20px 0' }">
					<!-- <BreadcrumbItem to="/">Home</BreadcrumbItem> -->
					<BreadcrumbItem v-for="(item, index) in $route.meta.breadList" :to="item.path" :key="index">{{
							item.name
					}}
					</BreadcrumbItem>
				</Breadcrumb>
				<Card>
					<router-view />
				</Card>
			</Content>
			<Footer class="layout-footer-center">personal &copy; Jayce Young</Footer>
		</Layout>
	</div>
</template>

<script>
export default {
	name: 'App',

	watch: {
		$route(newVal, oldVal) {
			// let temp = newVal.path.split('/');
			// console.log(temp, newVal);
		}
	},
}
</script>

<style scope>
#app {
	font-family: 'Avenir', Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	/* text-align: center; */
	color: #2c3e50;
	/* margin-top: 60px; */
}


.layout {
	border: 1px solid #d7dde4;
	background: #f5f7f9;
	position: relative;
	border-radius: 4px;
	overflow: hidden;
}

.layout-logo {
	width: 100px;
	height: 30px;
	background: #5b6270;
	border-radius: 3px;
	float: left;
	position: relative;
	top: 15px;
	left: 20px;
}

.layout-nav {
	width: 420px;
	margin: 0 auto;
	margin-right: 20px;
}

.layout-footer-center {
	text-align: center;
}
</style>



